div.bb8-container {
  display: flex;
  height: 200px;
  width: 150px;
  position: relative;
  justify-content: center; }
  div.bb8-container img {
    position: absolute; }
  div.bb8-container img.head {
    width: 70px;
    top: 2px;
    z-index: 1; }
  div.bb8-container img.body {
    width: 100px;
    bottom: 50px;
    -webkit-animation: bb8-roll 2s linear infinite;
            animation: bb8-roll 2s linear infinite; }
  div.bb8-container h4 {
    position: absolute;
    bottom: 0;
    margin: 0;
    padding: 5px;
    color: #f48525;
    font-family: "Roboto", sans-serif;
    font-size: 1.4rem;
    border: 1px solid white; }

@-webkit-keyframes bb8-roll {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes bb8-roll {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

